"use client";

import { useState } from "react";
import { useUser } from "@clerk/nextjs";
import { useRouter } from "next/navigation";
import { Navbar } from "@/components/navbar";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { toast } from "sonner";
import { FileText, Info, AlertCircle, CheckCircle2 } from "lucide-react";
import Link from "next/link";

const applicationSchema = z.object({
  minecraftUsername: z
    .string()
    .min(3, "Username must be at least 3 characters")
    .max(16, "Username must be at most 16 characters")
    .regex(/^[a-zA-Z0-9_]+$/, "Username can only contain letters, numbers, and underscores"),
  age: z
    .string()
    .optional()
    .refine(
      (val) => !val || (Number(val) >= 13 && Number(val) <= 100),
      "Age must be between 13 and 100"
    ),
  experience: z
    .string()
    .optional()
    .refine(
      (val) => !val || val.length >= 10,
      "Please provide at least 10 characters if filling this field"
    ),
  whyJoin: z
    .string()
    .min(50, "Please provide at least 50 characters explaining why you want to join")
    .max(1000, "Response is too long (max 1000 characters)"),
});

type ApplicationForm = z.infer<typeof applicationSchema>;

export default function ApplyPage() {
  const { isSignedIn, user } = useUser();
  const router = useRouter();
  const [loading, setLoading] = useState(false);

  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
  } = useForm<ApplicationForm>({
    resolver: zodResolver(applicationSchema),
  });

  const whyJoinValue = watch("whyJoin");
  const whyJoinLength = whyJoinValue?.length || 0;

  const onSubmit = async (data: ApplicationForm) => {
    if (!isSignedIn) {
      toast.error("Please sign in to submit an application");
      router.push("/sign-in");
      return;
    }

    setLoading(true);
    try {
      const response = await fetch("/api/apply", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
      });

      if (!response.ok) {
        const error = await response.json();
        throw new Error(error.error || "Failed to submit application");
      }

      toast.success("Application submitted successfully! We'll review it soon.");
      router.push("/dashboard");
    } catch (error) {
      toast.error(error instanceof Error ? error.message : "Failed to submit application");
    } finally {
      setLoading(false);
    }
  };

  if (!isSignedIn) {
    return (
      <div className="flex min-h-screen flex-col">
        <Navbar />
        {/* Main content centered vertically and horizontally */}
        <main className="flex-1 flex items-center justify-center">
          <div className="w-full max-w-xl px-4 space-y-6">
            <div className="flex flex-col items-center gap-4 text-center">
              <AlertCircle className="h-12 w-12 text-muted-foreground" />
              <h1 className="text-3xl font-bold leading-tight tracking-tighter md:text-5xl">
                Sign In Required
              </h1>
              <p className="max-w-[750px] text-lg text-muted-foreground">
                You need to be signed in to submit an application
              </p>
              <Button onClick={() => router.push("/sign-in")} size="lg">
                Sign In
              </Button>
            </div>
          </div>
        </main>
      </div>
    );
  }

  return (
    <div className="flex min-h-screen flex-col">
      <Navbar />
      {/* Center the main content within viewport using flex utilities */}
      <main className="flex-1 flex items-center justify-center">
        <div className="w-full max-w-2xl px-4 space-y-8">
          <div className="text-center space-y-2">
            <h1 className="text-3xl font-bold leading-tight tracking-tighter md:text-5xl lg:leading-[1.1]">
              Join AevumMC
            </h1>
            <p className="max-w-[750px] mx-auto text-lg text-muted-foreground">
              Fill out the application form below to join our server. We review applications regularly.
            </p>
          </div>

          <Alert>
            <Info className="h-4 w-4" />
            <AlertDescription>
              Applications are typically reviewed within 24-48 hours. You'll receive a notification
              once your application has been reviewed. Make sure to check your dashboard for updates.
            </AlertDescription>
          </Alert>

          <Card>
            <CardHeader>
              <div className="flex items-center gap-2">
                <FileText className="h-5 w-5 text-primary" />
                <CardTitle>Application Form</CardTitle>
              </div>
              <CardDescription>
                Please provide accurate information. All fields marked with * are required.
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
                <div className="space-y-2">
                  <Label htmlFor="minecraftUsername">
                    Minecraft Username <span className="text-destructive">*</span>
                  </Label>
                  <Input
                    id="minecraftUsername"
                    {...register("minecraftUsername")}
                    placeholder="Enter your exact Minecraft username"
                    className={errors.minecraftUsername ? "border-destructive" : ""}
                  />
                  {errors.minecraftUsername && (
                    <p className="text-sm text-destructive flex items-center gap-1">
                      <AlertCircle className="h-3 w-3" />
                      {errors.minecraftUsername.message}
                    </p>
                  )}
                  <p className="text-xs text-muted-foreground">
                    Make sure this matches your in-game username exactly (case-sensitive)
                  </p>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="age">Age (Optional)</Label>
                  <Input
                    id="age"
                    type="number"
                    min="13"
                    max="100"
                    {...register("age")}
                    placeholder="Your age"
                    className={errors.age ? "border-destructive" : ""}
                  />
                  {errors.age && (
                    <p className="text-sm text-destructive flex items-center gap-1">
                      <AlertCircle className="h-3 w-3" />
                      {errors.age.message}
                    </p>
                  )}
                  <p className="text-xs text-muted-foreground">
                    Must be 13 or older to play on our server
                  </p>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="experience">Minecraft Experience (Optional)</Label>
                  <Textarea
                    id="experience"
                    {...register("experience")}
                    placeholder="Tell us about your Minecraft experience. How long have you been playing? What do you enjoy most?"
                    rows={4}
                    className={errors.experience ? "border-destructive" : ""}
                  />
                  {errors.experience && (
                    <p className="text-sm text-destructive flex items-center gap-1">
                      <AlertCircle className="h-3 w-3" />
                      {errors.experience.message}
                    </p>
                  )}
                  <p className="text-xs text-muted-foreground">
                    This helps us understand your background and interests
                  </p>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="whyJoin">
                    Why do you want to join AevumMC? <span className="text-destructive">*</span>
                  </Label>
                  <Textarea
                    id="whyJoin"
                    {...register("whyJoin")}
                    placeholder="Please tell us why you're interested in joining our server. What aspects of SMP or PVP appeal to you? What would you bring to our community?"
                    rows={6}
                    className={errors.whyJoin ? "border-destructive" : ""}
                  />
                  <div className="flex items-center justify-between">
                    <div>
                      {errors.whyJoin && (
                        <p className="text-sm text-destructive flex items-center gap-1">
                          <AlertCircle className="h-3 w-3" />
                          {errors.whyJoin.message}
                        </p>
                      )}
                      {!errors.whyJoin && whyJoinLength > 0 && (
                        <p className="text-sm text-muted-foreground flex items-center gap-1">
                          <CheckCircle2 className="h-3 w-3 text-green-500" />
                          {whyJoinLength >= 50 ? "Great! " : ""}
                          {whyJoinLength} / 50 characters minimum
                        </p>
                      )}
                    </div>
                    <p className="text-xs text-muted-foreground">
                      {whyJoinLength} / 1000 characters
                    </p>
                  </div>
                  <p className="text-xs text-muted-foreground">
                    Minimum 50 characters. Be detailed and honest - this helps us get to know you!
                  </p>
                </div>

                <div className="pt-4 border-t">
                  <Button type="submit" className="w-full" size="lg" disabled={loading}>
                    {loading ? (
                      <>
                        <span className="mr-2">Submitting...</span>
                      </>
                    ) : (
                      <>
                        Submit Application
                      </>
                    )}
                  </Button>
                  <p className="text-xs text-center text-muted-foreground mt-2">
                    By submitting, you agree to follow our{" "}
                    <Link href="/rules" className="text-primary hover:underline">
                      server rules
                    </Link>
                  </p>
                </div>
              </form>
            </CardContent>
          </Card>
        </div>
      </main>
    </div>
  );
}